@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&family=Poppins:wght@100;200;300;400;500;600;700&display=swap');
*{margin:0;padding:0;list-style: none;text-decoration: none;font-family: 'Noto Sans KR','Poppins', sans-serif;}
.contents{max-width: 1200px;padding:0 40px;width:100%;margin:0 auto;}
.header{display: flex;align-items: center;justify-content: flex-start;background: rgb(27, 113, 139);color:#fff;height:58px;line-height: 58px;padding:0 40px;}
.header li{text-align: left;}
.list{width:100%;margin:0 auto;text-align: left;border-bottom: 1px solid #ddd;}
.list .title{font-weight:bold;padding:30px 0 0;}
.list .day{padding:20px 0; color: #999;}
.list .good{cursor: pointer;}
.contents .button{display: flex; align-items: center;justify-content: flex-start; padding:50px 0px;margin: 0 auto;}
.contents .button li{background: rgb(27, 113, 139);border-radius: 5px;margin:0 20px 0 0;color:#fff;height: 30px; line-height: 30px;padding:0 10px;}

.modal{margin-top: 20px;padding: 20px; background: #eee;text-align:left;}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
